/** @jsx jsx */
import { jsx, Styled } from "theme-ui"

const Link = ({ link, children, ...props }) => (
  <Styled.h2
    as="a"
    sx={{
      variant: "typography.link",
      overflowWrap: "break-word",
      display: "inline-block",
      height: "1.14em",
      maxWidth: "100%",
      ...props.style
    }}
    href={link}
    target={link.indexOf("http") !== -1 ? "_blank" : ""}
  >
    {children}
  </Styled.h2>
)

export default Link
